#sidebarMenu {
    position: absolute;
    top: 0px;
    /*left: -225px;*/
    height: 100vh;
    max-width: 225px;
    display: block;
    z-index: 9;
    background-color: rgba(59,104,123,0.5);
    color: rgb(255,255,255);
    padding: 1rem;
}

.hidden {
    /*visibility: hidden;*/

    -webkit-transition-timing-function: ease-in;

         -o-transition-timing-function: ease-in;

            transition-timing-function: ease-in;

    -webkit-transition: 1s;

    -o-transition: 1s;

    transition: 1s;

    -webkit-transform: translateX(-110%);

        -ms-transform: translateX(-110%);

            transform: translateX(-110%);
}

.shown {
    /*visibility: visible;*/
    -webkit-transition-timing-function: ease-out;
         -o-transition-timing-function: ease-out;
            transition-timing-function: ease-out;

    -webkit-transition: 1s;

    -o-transition: 1s;

    transition: 1s;

    -webkit-transform: translateX(-15px);

        -ms-transform: translateX(-15px);

            transform: translateX(-15px);
}

.asgard-menu-label {
    margin-top: 7rem;
    margin-bottom: 2rem;
}

.asgard-menu-item {
    text-align: center;
    padding: 0.5rem;
    cursor: pointer;
}

.last {
    position: inherit;
    bottom: 2rem;
    left: 1rem;
    right: 1rem;
}

.asgard-menu-item:hover {
    background-color: rgb(98,164,198);
}


